* {
    box-sizing: border-box;
}

body {
    background-color: #fafafa;
    margin: 0;
    font-family: "Roboto", sans-serif;
}
.container {
     width: 1440px;
     background-color: #fafafa;
     margin-left: auto;
     margin-right: auto;
     @media (max-width: 320px) {
       display: flex;
       flex-direction: column;
      width: 100%;
      }
}
